    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-right: 20px;
    padding: 10px;
    background: #EFF4FA;
    border: 1px solid #C1C1C1;
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.04);
    border-radius: 134px;
}
.admin-icon-box > div > i{
        font-size: 25px;
}
.admin-icon-box > p{
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #00264B;
    font-family: 'Rubik', sans-serif !important;
}
.admin-info-box{
    margin-bottom: 30px;
    margin-left: 100px;
    padding: 10px;
    width: 252px !important;
    background: rgba(239, 244, 250, 0.5);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.04);
    border-radius: 0px 10px 10px 10px;
}
.admin-info-box > p{
    font-weight: normal;
    font-size: 14px;
    line-height: 22px;
    color: #00264B;
    font-family: 'Rubik', sans-serif !important;
}
.admin-time{
    font-weight: normal;
    font-size: 11px;
    line-height: 14px;
    color: #C1C1C1;
    font-family: 'Rubik', sans-serif !important;
    margin-left: 200px;
}
