/* ? Select        */
.select-data-box{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.modal-select{
    width: 350px;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    color: #06BF69;
    padding: 10px;
    border: 2px solid #EFF4FA;
    border-radius: 10px;
    outline: none;
    font-family: 'Rubik', sans-serif !important;
}
.modal-title{
    margin-bottom: 7px;
    font-weight: 600;
    font-size: 14px;
    line-height: 22px;
    color: #00264B;
    font-family: 'Rubik', sans-serif !important;
}
.modal-btn-box{
    display: flex;
    align-items: center;
    justify-content: end;
}
.edit-btns{
    margin: 20px 0 0 10px !important;
    text-transform: capitalize !important ;
    display: flex;
    align-items: center;
    justify-content: end !important;
    background: #06BF69 !important;
    border-radius: 10px !important;
    padding: 9px 20px !important;
    font-weight: 600 !important;
    font-size: 14px !important;
    line-height: 22px;
    border: none;
    outline: none;
    cursor: pointer;
    color: #FFFFFF !important;
    font-family: 'Rubik', sans-serif !important;
}
.modal-name-small-box{
    margin-top: 20px;
    display: flex;
    align-items: center;
    border: 2px solid #EFF4FA;
    border-radius: 10px;
    padding: 5px 15px;
}
.modal-name-small-box > img{
    border-radius: 50%;
    margin-right: 10px;
}
.modal-name-small-box > h4{
    font-weight: normal;
    font-size: 15px;
    line-height: 22px;
    color: #00264B;
    font-family: 'Rubik', sans-serif !important;
}
.checkBox > span{
    font-weight: normal;
    font-size: 15px;
    line-height: 22px;
    color: #00264B;
    font-family: 'Rubik', sans-serif !important;
}
.modal-input-box{
    margin-top: 20px;
}
.modal-input-box > h4{
    margin-bottom: 3px;
    font-weight: 500;
    font-size: 15px;
    line-height: 22px;
    font-family: 'Rubik', sans-serif !important;
    color: #00264B;
}
.modal-input{
    padding: 15px;
    outline: none;
    width: 100% !important;
    background: none !important;
    border: 2px solid #EFF4FA !important;
    border-radius: 10px !important;
    font-family: 'Rubik', sans-serif !important;
    color: #00264B;
}
